.sidebar {
  width: 30%;
  padding: 20px;
  background-color: blue;
  @media (max-width: 768px) {
    width: 100%;
  }
  .profile {
    width: 100%;
    padding: 10px;
    @media (max-width: 768px) {
      display: flex;
      align-items: center;
    }
    @media (max-width: 568px) {
      flex-direction: column;
    }
    img {
      width: 100%;
      @media (max-width: 768px) {
        width: 50%;
      }
      @media (max-width: 568px) {
        width: 100%;
      }
    }
    .profile-text {
      // padding: 20px;
      color: white;
      width: 100%;
      @media (max-width: 768px) {
        width: 50%;
      }
    
       @media (max-width: 568px) {
        width: 100%;
        padding: 0;
      }
      .details {
        .con {
          justify-content: flex-start;
          @media (max-width: 328px) {
            flex-direction: column;
            justify-content: start;
            align-items: start;
            margin: 20px 0;
          }
       
          @media (max-width: 798px) {
            flex-direction: row;
            justify-content: start;
            align-items: start;
            margin: 20px 0;
          }
          .icon {
            padding: 10px;
            font-size: 1.6em;
            margin-right: 20px;
           
            @media (max-width: 328px) {
              padding: 0;
              margin: 0;
              // margin-bottom: 10px;
              font-size: 1em;
            }
          }
        }
      }
    }
  }

  .social{
    padding: 10px;
    h2{
      font-size: 2rem;
    }
    hr{
      margin: 10px 0;
    }
    .social-links{
      li{
        a{
          font-size: 35px;
          color: gold;
        }
      }
    }
  }
}
