* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: gilroy;
  transition:.1s ease-in;
}

.flex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
}

.flex-start {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  list-style: none;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
}

.column {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  list-style: none;
}

.wrapper {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  .cv{
    position: relative;
    background-color: inherit;
    @media (max-width:768px) {
      flex-direction: column;
    }
    .toggle{
      position: absolute;
      top: 50px;
      left: 50px;
      padding: 10px;
      font-size: 1.2em;
      // background-color: inherit;x  
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 40px;
    }
  }
}
.theme {
  background-color: rgb(255, 255, 255);
  color: black;
}
.dark {
  background-color: rgb(0, 0, 0);
  color: gold;
}
