.main {
  padding: 20px;
  .bio {
    font-size: 3em;
    font-weight: bold;
    text-transform: capitalize;
  }
  hr {
    width: 50%;
    border: solid 4px gold;
    margin: 20px 0;
  }
  p {
    line-height: 20px;
  }

  .experience {
    h1 {
      font-size: 2em;
      font-weight: bold;
      margin: 20px 0;
    }
    .job {
      margin: 20px 0;
      @media (max-width: 478px) {
        flex-direction: column;
        text-align: left;
      }
      h2 {
        width: 200px;
        // height: 100%;
        padding: 10px;
        background-color: blue;
        text-align: center;
        margin-right: 20px;
        @media (max-width:478px) {
          font-size: 1.2em;
        }
      }
      .details- {
        h3 {
          font-size: 1.4em;
          font-weight: 700;
          @media (max-width: 478px) {
            font-size: 1.2em;
          }
        }
        h5 {
          font-size: 1rem;
          font-weight: 500;
          font-style: italic;
          @media (max-width: 478px) {
            font-size: 0.9em;
          }
        }
        p {
          display: none;
        }
      }
    }
  }
}
